
html {font-size: 20px
}
#app {
  font-family: PingFang SC, Microsoft Yahei;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
}

.todo {
  width: 80%;
  margin: 0 auto;
  border: 1px solid #ddd;
}
.todo__header {
  padding: 0.2rem 1rem/* 20px */;
  border-bottom: 1px solid #ddd;
  background-color: #eee;
}

.item {
  padding: 1rem/* 20px */;
  font-size: .8rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
}
.item--done {
  color: green;
}
.item--expired {
  color: red;
}
.item__main {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin-right: 1rem/* 20px */;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
}
.item__time {
  margin-left: 1rem/* 20px */;
  font-size: .7rem/* 12px */;
  color: #666;
}
.item__opts__item {
  margin-right: .3rem;
  font-size: .7rem/* 12px */;
  text-decoration: none;
}
.item__input {
  padding: .2rem/* 4px */ .4rem/* 8px */;
  margin-right: .5rem/* 10px */;
  border: 1px soild #ddd;
}
.item__input--title {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
